<template>
	<v-card>
		<v-card-title class="py-2">Pilih Startup</v-card-title>
		<v-card-text>
			<Loader v-if="isLoading" />
			<v-data-table
				v-else
				class="mt-0"
				:headers="header"
				:items="dataStartup"
				:items-per-page="10">	
				<template v-slot:item.no="{item}">
					{{dataStartup.map(x => x.id).indexOf(item.id)+1}}
				</template>
				<template v-slot:no-data>
					<v-alert type="info" class="mt-4">
						<div>Data tidak ditemukan</div>
					</v-alert>
				</template>				
				<template v-slot:item.actions="{ item }">
					<v-btn
						small dark
						@click="$router.push({ name: 'show-response', params: {startup_id: item.id_startup, traction_id: traction_id} })"
						color="cyan"
						depressed>
						lihat traction
					</v-btn>
				</template>	
			</v-data-table>
		</v-card-text>
	</v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import Loader from '@/components/Loader'
	export default{
		props: ['traction_id'],

		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Startup', value: 'name', sortable: false },
				{ text: 'Managed By', value: 'manager_name', sortable: false },
				{ text: '', value: 'actions', align: 'center', sortable: false }
			],
		}),		
    components: { Loader },
    created() {
      this.$store.dispatch('dataIncubatorTraction/updateIsLoading', true)
      this.$store.dispatch('dataIncubatorTraction/getDetailTraction', this.traction_id)
    },
    computed: {
      ...mapState('dataIncubatorTraction', ['dataStartup', 'isLoading']),
    }
	}
</script>